@import './button.scss';
.addWrap {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #d9d9d9;
  .button {
    margin-left: 5px;
    &.disabled{
      background-color: #d9d9d9;
      border-color: #d9d9d9;
      text-shadow: none;
      box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
      color: #aaa;
    }
  }
  input, textarea {
    width: 40%;
    padding: 10px;
    font-size: 14px;
    border-radius: 4px;
    border: 1px solid #d9d9d9;
    color: #333;
  }
  .btnWrap {
    margin-top: 15px;
    text-align: center;
  }
}

.row {
  label {
    min-width: 5%;
    display: inline-block;
    margin-right: 5px;
    font-weight: bold;
    vertical-align: top;
  }
  & + .row {
    margin-top: 10px;
  }
}